@import url("https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,300;0,400;0,700;1,300;1,400;1,700&display=swap");
.header {
  width: 100%;
  height: 8rem;
  background: #f5f5f5;
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: fixed;
  top: 0;
  z-index: 20;
  transition: all 0.3s cubic-bezier(0.39, 0.575, 0.565, 1);
}
.header .icon__menu {
  width: 8rem;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-left: 1rem;
}
.header .icon__menu i {
  font-size: 2rem;
  cursor: pointer;
  position: absolute;
  color: #3b5895;
}
.header h1 {
  color: #3b5895;
}
@media (max-width: 480px) {
  .header h1 {
    font-size: 2.4rem;
  }
}
.header .logo {
  width: 15rem;
  height: 6rem;
  margin-right: 3rem;
}
@media (max-width: 768px) {
  .header .logo {
    width: 10rem;
  }
}
@media (max-width: 480px) {
  .header .logo {
    width: 8rem;
    height: 4.5rem;
  }
}